
import {mapMutations} from 'vuex'
import XImg from 'vux/src/components/x-img/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import VScroll from '../components/VScroll'
import {company} from '../config'
export default {
  head: {
    title: {
      inner: '车险报价'
    }
  },
  components: {
    XImg,
    VScroll,
    Loading
  },
  created () {
    this.getOfferList()
  },
  methods: {
    getOfferList () {
      this.$http({
        method: 'jsonp',
        url: company,
        jsonp: 'callback',
        jsonpCallback: 'json',
        before: () => {
        }
      })
      .then(res => {
        this.list = res.body.data.companyList
      })
    },
    handleSaveData (item) {
      this.$localStorage.set('orderCompany', JSON.stringify({
        companyId: item.id,
        banner: item.bigPic,
        companyName: item.name
      })) // 存储订单公司信息
    },
    ...mapMutations({
      showLoading: 'updateLoadingStatus'
    })
  },
  data () {
    return {
      list: []
    }
  }
}



